import React,{useState,lazy,Suspense,useEffect} from 'react';
import HeaderComponent from '../header/header';
import Footer from '../footer/footer';
import {BrowserRouter, Routes, Route} from 'react-router-dom'
import { Box, CircularProgress } from "@material-ui/core";
import commonStyles from "../style/commonStyle";
import axios from 'axios';

const Dashboard = lazy(()=>import('../dashboard/dashboard'));
const TermsAndConditions = lazy(()=>import('../terms/terms'))

function Mainapp() {
    const classes = commonStyles();
    const [mobileOpen,setMobileOpen] = useState(false);
    const [latestRelease, setLatestRelease] = useState(null);
    
    const toggleMobileMenu = ()=> {
        setMobileOpen(!mobileOpen);
    }

    const downloadReleaseFile = async () => {
        try {
            const link = document.createElement('a');
            link.href = 'https://github.com/Rejjak/binamateweb/releases/download/v1/billApp.zip';
            link.download = 'billApp.zip';
            link.click();
        } catch (error) {
            alert("I am working on it, please try after sometime");
        }
    };

    const getData = async () => {
        //7 days valid only
        const token = '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';
        const response = await axios.get(
            'https://api.github.com/repos/Rejjak/billapp/releases/latest',
            {
              headers: {
                Authorization: `Bearer ${process.env.REACT_APP_GIT_ACCESS_TOKEN}`,
              },
            }
          );
        console.log(response.data);
        setLatestRelease(response.data);
    };

    useEffect(()=>{
        getData();
    },[]);

    return (
        <BrowserRouter basename={window.location.pathname || ''}>
         {/* <BrowserRouter> */}
            <HeaderComponent latestRelease={latestRelease} onDownload={()=>downloadReleaseFile()}/>
            <Box className={classes.wrapper}>
                <Suspense>
                    <Routes>
                        <Route exact path='/' element={<Dashboard latestRelease={latestRelease} onDownload={()=>downloadReleaseFile()}/>} />
                        <Route path='/terms-conditions' element={<TermsAndConditions/>} />
                    </Routes>
                </Suspense>
            </Box>
            <Footer/>
        </BrowserRouter>
    );
}

export default Mainapp;